 .style__cta {
     color: white;
     display: flex;
     justify-content: space-between;
     flex-direction: row;
     align-items: center;
     padding: 2rem;
     outline: none;
     border-radius: 1rem;
     background: var(--gradient-bar);
 }

 .style__cta-content {
     font-family: var(--font-family);
     font-weight: 500;
     line-height: 45px;
     font-size: 20px;
     color: black;
 }

 .style__cta-btn {
     display: flex;
     align-items: center;
     justify-content: center;
     margin-left: 2rem;
 }

 .style__cta-btn button {
     background: #000000;
     border-radius: 2rem;
     border: none;
     outline: none;
     color: #fff;
     padding: 1rem 2rem;
     cursor: pointer;
     font-size: 16px;
     font-family: var(--font-family);
 }

 @media screen and (max-width : 1050px) {
     .section__margin {
         margin: 3rem 2rem;
     }

     .style__cta-content {
         line-height: 36px;
     }

     .style__cta-btn button {

         padding: 16px 2rem;
         font-size: 21px;
         width: max-content;
     }
 }

 @media screen and (max-width : 750px) {
     .section__margin {
         margin: 2rem 4rem;
     }

     .style__cta-content {
         line-height: 36px;
     }

     .style__cta {
         flex-direction: column;
         align-items: flex-start;
         padding: 1rem 2rem;
     }

     .style__cta-btn {
         margin-left: unset;
         margin: 1rem 0rem;
     }

     .style__cta-btn button {
         border-radius: 5px;
     }
 }

 @media screen and (max-width : 550px) {
     .section__margin {
         margin: 0rem 1rem;
     }

     .style__cta-content {
         line-height: 36px;
     }

     .style__cta {
         flex-direction: column;
         align-items: flex-start;
         padding: 1rem 2rem;
     }

     .style__cta-btn {
         margin-left: unset;
         margin: 1rem 0rem;
     }

     .style__cta-btn button {
         border-radius: 5px;
     }
 }