 .style__blog-container-article {
     width: 100%;
     height: 100%;
     display: flex;
     flex-direction: column;
     background: var(--color-footer);
 }

 .style__blog-container-article-image {
     width: 100%;
     height: 100%;
     background: var(--color-footer);
 }

 .style__blog-container-article-image img {
     width: 100%;
     height: 100%;
 }

 .style__blog-container-article-content {
     display: flex;
     flex-direction: column;
     justify-content: space-between;
     padding: 1rem 1.5rem;
     height: 100%;
 }

 .style__blog-container-article-content p:first-child {
     font-family: var(--font-family);
     font-size: 16px;
     font-weight: 300;
     color: #fff;
     line-height: 35px;
 }

 .style__blog-container-article-content h3 {
     font-family: var(--font-family);
     font-size: 25px;
     font-weight: 500;
     color: #fff;
     line-height: 35px;
     margin-bottom: 5rem;
     cursor: pointer;
 }

 .style__blog-container-article-content p:last-child {
     cursor: pointer;
     color: #fff;
     font-family: var(--font-family);
 }

 @media screen and (max-width : 1027px) {
    
 }

 @media screen and (max-width : 550px) {
     .style__blog-container-article-content {
         font-size: 18px;
         line-height: 25px;
     }
 }