.style__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 1rem;
}

.style__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.style__navbar-links_logo {
    margin-right: 2rem;
}

.style__navbar-links_logo img {
    width: 60px;
    height: 20px;
}

.style__navbar-links_container {
    display: flex;
    flex: none;
    justify-content: space-evenly;
}

.style__navbar-sign p,
.style__navbar-links_container p a,
.style__navbar-menu_container p a,
.style__navbar-menu_container-links-sign p {
    font-family: var(--font-family);
    color: white;
    text-decoration: none;
    margin: 12px;
    line-height: 50px;
    font-weight: 500;
    cursor: pointer;
    font-size: 18px;
}

.style__navbar-sign {
    display: flex;
    flex: none;
    align-items: center;
    justify-content: space-around;
    margin-left: auto;
}

.style__navbar-sign button,
.style__navbar-menu_container button {
    padding: 0.5rem 1.5rem;
    color: #fff;
    background: rgb(222, 87, 4);
    border: none;
    font-family: var(--font-family);
    outline: none;
    border-radius: 2px;
    cursor: pointer;
    line-height: 20px;
    /* font-size: 18px; */
}

/*  responsive for mobile device */
.style__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.style__navbar-menu_container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    margin-top: 2rem;
    min-width: 210px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
    position: absolute;
    right: 0px;
}

/* .style__navbar-menu_container p {
    margin: 1rem 0rem;
} */

.style__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width : 1050px) {
    .style__navbar-links_container {
        display: none;
    }

    .style__navbar-menu {
        display: flex;
        /* display: block; */
    }

}

@media screen and (max-width : 700px) {
    .style__navbar-menu {
        padding: none;
    }

    .style__navbar-sign {
        display: none;
    }

    .style__navbar-menu_container-links-sign {
        display: block;
    }
}

@media screen and (max-width : 550px) {
    .style__navbar-menu {
        padding: none;
    }

    .style__navbar-menu_container-links-sign {
        display: block;
    }
}