.style__possibility {
    display: flex;
    flex-direction: row;
}

.style__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;
}

.style__possibility-image img {
    width: 100%;
    height: 100%;
}

.style__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.style__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: rgb(45, 227, 227);
    margin-bottom: 1rem;
}

.style__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 34px;
    /* line-height: 45px; */
    color: rgb(45, 227, 227);
    margin-bottom: 1rem;
}

.style__possibility-content p {
    font-family: var(--font-family);
    font-size: 16px;
    color: var(--color-text);
    margin-bottom: 2rem;
}



.style__possibility-content h4:last-child {
    color: #FF8A71;
}

@media screen and (max-width : 1050px) {

    .style__possibility {
        flex-direction: column;
        margin: 0rem 2rem;
    }

    .style__possibility-content h4:first-child {
        font-size: 23px;
        margin-top: 1rem;
    }

    .style__possibility-content h4:last-child {
        font-size: 23px;
    }

    .style__possibility-content h1 {
        font-size: 50px;
    }

    .style__possibility-content p {
        font-size: 19px;
        margin-right: 3rem;
    }
}

@media screen and (max-width : 550px) {
    .style__possibility {
        margin: 0rem 0rem;
    }
}