 .gradient__text {
     font-family: var(--font-family);
     color: var(--color-text);
 }

 .style__header {
     display: flex;
     align-items: center;
 }

 .style__header-content h1 {
     flex: 1;
     display: flex;
     justify-content: center;
     align-items: flex-start;
     flex-direction: column;
     letter-spacing: -0.04rem;
 }

 .style__header-content p {
     font-family: var(--font-family);
     font-size: 20px;
     line-height: 27px;
     color: var(--color-text)
 }

 .style__header-content_input {
     width: 100%;
     margin: 2rem 0 1rem;
     display: flex;
 }

 .style__header-content_input input {
     flex: 2;
     width: 100%;
     background: #052D56;
     min-height: 50px;
     padding: 1rem 2rem;
     border: none;
     outline: none;

     /* line-height: 27px; */
     font-size: 20px;
     color: #fff;
     font-family: var(--font-family);

     /* border-radius: 5px 0px 0px px; */
     border-top-left-radius: 5px;
     border-bottom-left-radius: 5px;
 }

 .style__header-content_input button {
     background: #FF4820;
     cursor: pointer;
     min-height: 50px;
     padding: 1rem 2rem;
     border: none;
     outline: none;
     /* line-height: 27px; */
     font-size: 20px;
     color: #fff;
     font-family: var(--font-family);
     /* border-radius: 5px;   */
     border-top-right-radius: 5px;
     border-bottom-right-radius: 5px;
 }

 .style__header-content_people {
     width: 100%;
     display: flex;
     align-items: center;
     justify-content: space-between;
     margin-top: 2rem;
 }

 .style__header-content_people img {
     width: 200px;
     height: 38px;
 }

 .style__header-content_people p {
     margin-left: 1rem;
     font-family: var(--font-family);
     font-weight: 500;
     font-size: 16px;
     color: #fff;
     text-align: right;
 }

 .style__header-image_ai {
     flex: 1;
     display: flex;
     justify-content: center;
     align-items: center;
     margin-left: 3rem;
 }

 .style__header-image_ai img {
     /* width: 100%; */
     width: 25rem;
     height: 100%;
 }

 @media screen and (max-width : 1050px) {
     .section__padding {
         padding: 4rem 2rem;
     }

     .style__header {
         flex-direction: column;
     }

     .style__header-content h1 {
         font-size: 48px;
         line-height: 50px;
     }

     .style__header-content {
         margin: 0 0 3rem;
     }

     .style__header-content_input input {
         font-size: 21px;
         width: fit-content;
         width: 100%;
         border-radius: 5px;
     }

     .style__header-content_input input,
     .style__header-content_input button {
         font-size: large;
         width: initial;
         height: fit-content;
         padding: 20px;
     }

     .style__header-image_ai {
         margin-left: unset;
     }

     .style__header-image_ai img {
         width: 100%;
         /* width: 25rem; */
     }
 }

 @media screen and (max-width : 650px) {
     .style__header-content h1 {
         font-size: 35px;
         line-height: 50px;
     }


     .style__header-content_people {
         flex-direction: column;
         justify-content: center;
     }

     .style__header-content_input input,
     .style__header-content_input button {
         font-size: large;
         width: initial;
         height: fit-content;
         padding: 20px;
     }

     .style__header-image_ai img {
         width: 100%;
         /* width: 25rem; */
     }
 }

 @media screen and (max-width : 550px) {
     .style__header-content_people {
         flex-direction: column;
     }

     .style__header-content_input {
         border-radius: 5px;
         flex-direction: column;
     }

     .style__header-content_input button {
         width: fit-content;
         margin: 1rem 0rem;
         border-radius: 5px;
     }
 }