.style__features {
    width: 100%;
    display: flex;
    align-content: center;
    justify-content: space-between;
}

.style__features-heading {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    text-align: left;
    margin-right: 5rem;
}

.style__features-heading h1 {
    font-size: 27px;
    line-height: 45px;
}

.style__features-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: rgb(244, 105, 55)
}

.style__features-container {
    flex: 1.5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

@media screen and (max-width : 1050px) {

    .style__features {
        flex-direction: column;
    }
}

@media screen and (max-width : 650px) {

    .style__features-container-feature-title h1 {
        line-height: normal;
        font-size: 18px;
    }
}

@media screen and (max-width : 550px) {
    .style__features-heading h1 {
        line-height: normal;
    }

    .style__features-container-feature-title h1 {
        line-height: normal;
        font-size: 18px;
    }

    .style__features-heading {
        margin-right: unset;
    }
}