 .style__whatgpt3 {
     display: flex;
     flex-direction: column;
     background: var(--color-footer);
     padding: 2rem;

     /* ff 3.6+ */
     background: -moz-radial-gradient(circle at 5% 14%, rgba(44, 47, 110, 1) 0%, rgba(17, 35, 107, 1) 50%, rgba(21, 21, 56, 1) 100%);

     /* safari 5.1+,chrome 10+ */
     background: -webkit-radial-gradient(circle at 5% 14%, rgba(44, 47, 110, 1) 0%, rgba(17, 35, 107, 1) 50%, rgba(21, 21, 56, 1) 100%);

     /* opera 11.10+ */
     background: -o-radial-gradient(circle at 5% 14%, rgba(44, 47, 110, 1) 0%, rgba(17, 35, 107, 1) 50%, rgba(21, 21, 56, 1) 100%);

     /* ie 10+ */
     background: -ms-radial-gradient(circle at 5% 14%, rgba(44, 47, 110, 1) 0%, rgba(17, 35, 107, 1) 50%, rgba(21, 21, 56, 1) 100%);

     /* global 92%+ browsers support */
     background: radial-gradient(circle at 5% 14%, rgba(44, 47, 110, 1) 0%, rgba(17, 35, 107, 1) 50%, rgba(21, 21, 56, 1) 100%);

 }

 .style__whatgpt3-feature {
     display: flex;
 }

 .style__whatgpt3-feature .style__features-container_featue {
     margin: 0px;
 }

 .style__whatgpt3-feature .style__features-container-feature-text {
     max-width: 700px;
 }

 .style__whatgpt3-heading {
     display: flex;
     flex-direction: row;
     align-items: baseline;
     justify-content: space-between;
     margin: 4rem 0 2rem;
 }

 .style__whatgpt3-heading h1 {
     font-size: 34px;
     line-height: 45px;
     font-weight: 800px;
     font-family: var(--font-family);
     max-width: 510px;
 }

 .style__whatgpt3-heading p {
     font-size: 20px;
     line-height: 26px;
     font-weight: 800px;
     font-family: var(--font-family);
     color: var(--color-subtext);

 }

 .style__whatgpt3-feature-container {
     display: flex;
     align-items: baseline;
     flex-direction: column;
     justify-content: space-between;
     margin-top: 2rem;
 }

 @media screen and (max-width : 1050px) {
     .section__margin {
         margin: 4rem 1rem;
     }

     .style__whatgpt3-heading h1 {
         font-size: 30px;
         line-height: 39px;
         margin-right: 1rem;
     }

     .style__whatgpt3-heading p {
         font-size: 18px;
         line-height: 25px;
         margin-left: 2rem;
     }
 }

 @media screen and (max-width : 650px) {
     .section__margin {
         margin: 2rem 1rem;
     }

     .style__whatgpt3-heading h1 {
         font-size: 30px;
         line-height: 39px;
         margin-right: 1rem;
     }

     .style__whatgpt3-heading p {
         font-size: 15px;
         line-height: 25px;
     }

     .style__whatgpt3-feature-container {
         flex-direction: column;
     }
 }

 @media screen and (max-width : 350px) {
     .section__margin {
         margin: unset;
     }

     .style__whatgpt3-heading h1 {
         font-size: 19px;
         line-height: 20px;
         margin-right: 1rem;
     }

     .style__whatgpt3-heading p {
         font-size: 15px;
         line-height: 25px;
     }

     .style__whatgpt3-feature-container {
         flex-direction: column;
     }
 }

 @media screen and (max-width : 280px) {
     .section__margin {
         margin: 4rem 0rem;
     }

     .style__whatgpt3-heading p {
         margin-left: unset;
     }
 }