 .style__footer {
     /* border: 1px solid whitesmoke; */
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     background: var(--color-footer);
     padding-bottom: 2rem;
 }

 .style__footer-heading {
     width: 100%;
     text-align: center;
     margin-bottom: 3rem;
 }

 .style__footer-heading h1 {
     font-family: var(--font-family);
     font-weight: 500;
     font-size: 62px;
     text-align: center;
     letter-spacing: 1px;
 }

 .style__footer-btn {
     display: flex;
     align-items: center;
     justify-content: center;
 }

 .style__footer-btn p {
     border: 2px solid rgb(245, 245, 245);
     color: #fff;
     line-height: 22px;
     font-size: 40px;
     font-family: var(--font-family);
     padding: 2rem;
     margin-bottom: 6rem;
     cursor: pointer;
 }

 .style__footer-btn p a {
     color: #fff;
     text-decoration: none;
 }

 .style__footer-links {
     display: flex;
     align-items: flex-start;
     justify-content: space-between;
     flex-wrap: wrap;
     flex-direction: row;
     width: 100%;
     text-align: left;
     margin-left: 7rem;
 }

 .style__footer-links div {
     width: 250px;
     margin: 1rem;
 }

 .style__footer-links-logo {
     display: flex;
     flex-direction: column;
 }

 .style__footer-links-logo img {
     width: 118px;
     height: 30px;
     margin-bottom: 1rem;
 }

 .style__footer-links-logo p {
     font-family: var(--font-family);
     color: var(--color-text);
     width: 10rem;
     text-align: left;
     font-size: 16px;
 }

 .style__footer-links-div {
     display: flex;
     flex-direction: column;
     justify-content: flex-start;
 }

 .style__footer-links-div h4 {
     font-family: var(--font-family);
     font-size: 14px;
     line-height: 14px;
     color: #fff;
     font-weight: 100;
     margin-bottom: 1rem;
 }

 .style__footer-links-div p {
     font-family: var(--font-family);
     font-size: 14px;
     line-height: 14px;
     color: #fff;
     font-weight: 100;
     margin-bottom: 1rem;
 }

 .style__footer-links-div p a {
     text-decoration: none;
     color: #fff;
 }

 .style__footer-copyright {
     margin-top: 2rem;
     text-align: center;
     width: 100%;

 }

 .style__footer-copyright p {
     font-family: var(--font-family);
     font-size: 16px;
     line-height: 14px;
     color: #fff;
     font-weight: 100;
     text-align: center;
 }

 @media screen and (max-width : 1050px) {
     .style__footer-links {
         margin-left: unset;
     }
 }

 @media screen and (max-width : 750px) {
     .style__footer-heading h1 {
         font-size: 47px;
         letter-spacing: 0px;
     }

     .style__footer-btn p {
         font-size: 27px;
         padding: 1rem;
     }

     .style__footer-links {
         margin-left: unset;
     }

     .style__footer-copyright p {
         line-height: 30px;
     }
 }

 @media screen and (max-width : 350px) {
     .style__footer-btn p {
         font-size: 20px;
         padding: 10px;
     }

     .style__footer-copyright p {
         line-height: 30px;
     }
 }