 .style__features-container-feature {
     width: 100%;
     display: flex;
     align-content: flex-start;
     justify-content: space-between;
     margin: 1rem;
     margin-left: auto;
 }

 .style__features-container-feature-title {
     flex: 1;
     max-width: 180px;
     margin-right: 2rem;
 }


 .style__features-container-feature-title h1 {
     font-family: var(--font-family);
     color: var(--color-text);
     font-size: 25px;
     font-weight: 500;
     line-height: 25px;
     width: max-content;
     margin: 5px 0rem;
     max-width: fit-content;
 }


 .style__features-container-feature-title div {
     width: 38px;
     height: 3px;
     background: var(--gradient-bar);
     border-radius: 5px;
     box-shadow: 0 4px 4px rgba(0, 0, 0, 0.5);
     margin: 5px 0px;
 }

 .style__features-container-feature-text {
     display: flex;
     flex: 2;
     max-width: 475px;
 }

 .style__features-container-feature-text p {
     font-family: var(--font-family);
     font-weight: 500px;
     color: var(--color-text);
 }

 @media screen and (max-width : 1050px) {
     .section__margin {
         margin: 4rem 3rem;
     }

     .style__features-heading p {
         line-height: normal;
         margin-top: 2rem;
     }

     /* .style__features-container-feature {
         flex-direction: column;
     } */
 }

 @media screen and (max-width : 350px) {
    
     .style__features-container-feature {
         flex-direction: column;
     }
 }