 .style__blog {
     display: flex;
     flex-direction: column;
 }

 .style__blog-heading {
     width: 100%;
     text-align: left;
     margin-bottom: 5rem;

 }

 .style__blog-heading h3 {
     font-family: var(--font-family);
     font-size: 5rem;
     font-weight: 500;
     line-height: 75px;
     letter-spacing: -0.04em;
 }

 .style__blog-container {
     display: flex;
     flex-direction: row;
 }

 .style__blog-container-groupA {
     flex: 1;
     margin-right: 2rem;
 }

 .style__blog-container-groupB {
     flex: 1;
     display: grid;
     grid-template-columns: repeat(2, 1fr);
     grid-gap: 2rem;
 }

 @media screen and (max-width : 1050px) {
     .style__blog-container {
         flex-direction: column-reverse;
     }

     .style__blog-container-groupA {
         margin-right: unset;
         margin-top: 2rem;
     }

     .style__blog-container-groupA {
         width: 48%;
     }
 }

 @media screen and (max-width : 550px) {
     .style__blog-container-article-content h3 {
         font-family: var(--font-family);
         font-size: 20px;
         line-height: 25px;
         margin-bottom: 2rem;
     }

     .style__blog-container-article-content p:last-child {
         font-size: 15px;
     }
 }

 @media screen and (max-width : 400px) {
     .style__blog-container-groupB {
         grid-template-columns: repeat(1, 1fr);
     }

     .style__blog-container-groupA {
         width: 100%;
     }
 }